const OpenSourceProject = (props: {
    project: ProjectType
}) => {
    const CurrentProjectType = ProjectTypesData[props?.project?.type];
    return (
        <div className="col-lg-4 col-md-6 open-source-project-item">
            <a href={props?.project?.link} target="_blank">
                <div className="item-container" style={{
                    border: `2px solid ${CurrentProjectType?.backgroundColor}`,
                }}>
                    <div className="project-image-container" style={{ backgroundColor: CurrentProjectType?.backgroundColor }}>
                        <img
                            src={CurrentProjectType?.whiteImage}
                            style={{
                                height: CurrentProjectType?.whiteHeight
                            }}
                        />
                    </div>
                    <div className="project-key">
                        {props?.project?.key}
                    </div>
                    <div className="project-description">{props?.project?.description}</div>
                    <div className="project-link">
                        <a target="_blank" role="button"
                            style={{
                                backgroundColor: CurrentProjectType?.backgroundColor,
                                borderColor: CurrentProjectType?.backgroundColor,
                                color: CurrentProjectType?.color
                            }}
                            href={props?.project?.link}
                            className="btn btn-primary">
                            {props?.project?.key}'ı ziyaret et
                        </a>
                    </div>
                </div>
            </a>

        </div>
    )
}

const sWidth: number = window.innerWidth;

const OpenSource: React.FunctionComponent<{
    hideTitle?: boolean
}> = (props) => {

    return (
        <div className="container-fluid open-source-project-container">

            <h1 style={{
                ...props?.hideTitle && (
                    { display: "none" }
                )
            }}>
                Açık Kaynak Projelerim
            </h1>
            <div className="container open-source-project" style={{
                ...props?.hideTitle && (
                    sWidth < 992 && {
                        marginTop: "-40px"
                    }
                )
            }}>
                <div className="row open-source-project-row">
                    {(OpenSourceProjectData || []).map((project, index) => {
                        return <OpenSourceProject project={project} />
                    })}
                </div>
            </div>
        </div>


    )
}

export default OpenSource;